.button {
    cursor: pointer;
    background-color: rgb(255, 255, 255);
    border: none;
    padding: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 32px;
}

.button.textButton {
    padding: 0 8px;
    border-radius: 8px;
    background-color: rgb(240, 240, 240);
}

.button.iconButton {
    border-radius: 50%;
}
.button:hover {
    background-color:  rgb(230, 230, 230);
}

.button:active {
    background-color: rgb(210, 210, 210);
}

.button:disabled {
    opacity: 0.6;
    background-color: rgb(0, 0, 0, 0.1);
}

.button.positive {
    background-color: rgb(20, 94, 34);
    color: rgb(240, 240, 240);
}
.button.negative {
    background-color: rgb(94, 20, 20);
    color: rgb(240, 240, 240);
}
